import React from "react";

const Player = ({ onPlay, onStop, isPlaying }) => {
  return (
    <div>
      <h1>sonify demo</h1>
      <button type="button" onClick={() => (isPlaying ? onStop() : onPlay())}>
        <span role="img" aria-label="play">
          ▶️
        </span>
        {isPlaying ? "Stop" : "Listen"}
      </button>
    </div>
  );
};
export default Player;
